if (typeof olProjProj4.get() !== 'function' && typeof proj4 === 'function') {  // nowebpack
  olProj.setProj4(proj4);  // nowebpack
}  // nowebpack

// webpack: proj4.Proj.projections.add(somerc);
const exports = 'somerc';


export default exports;



